body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin :0 ;
  padding :0;
  box-sizing:border-box;
  font-size:20px;
}


.app{
  background :linear-gradient(#7b7b7b,#5ccab5);
  min-height:100vh ;
  display:flex;
  align-items: center;
  justify-content:center;
}

.form {
  background :#2c5046;
  padding: 70px 50px;

}

input , button {
  display: block;
  width:100%;
  margin: auto;
  margin-bottom: 10px;
  outline:none ;
  padding: 10px 0;
  box-shadow: 0;
  border:0;
  
}

input {
  background: #9fd1c6;
  border-bottom: solid 1px #9fd1c6 ;
  background :transparent

}

button {
  color:#fff;
  background:#000;
  border:0;
  cursor:pointer;
  margin-top:20px;
}

.info {
  width: 100%;
  letter-spacing: 2px;
 
}

.info-key{
  color: white;
  font-weight: 400;
  border-bottom: solid 2px rgba(25,255,255,0.06);
  padding: 5px;
}

.info-key:last-child{
  border: 0;
}

.info-value{
  font-weight: 200;
}